import { Collapse } from "antd";
import { CInput, CTextArea } from "@components";


const CFixAssetReferenceNotes= ()=>{
    const { Panel } = Collapse
    return (
      <>
         <Collapse defaultActiveKey={["0"]} style={{ marginTop: 10 }}>
            <Panel header="Reference and Notes" key="0">
               <CInput
                  label="Reference"
                  name="reference"
                  id="reference"
                  autoFocus
               />

               <CTextArea
                  label="Comments"
                  name="comments"
                  id="comments"
                  autoFocus
               />

               <CInput
                  label="Document Location"
                  name="document_location"
                  id="document_location"
                  autoFocus
               />

               <CTextArea
                  label="Disposal Restrictions"
                  name="disposal_restrictions"
                  id="disposal_restrictions"
                  autoFocus
               />
            </Panel>
         </Collapse>
      </>
    )
}
export default CFixAssetReferenceNotes;